import {
  ProjectReportId,
  ProjectStatusCode,
  ReportText,
  Title,
} from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProjectStatusUpdateReport extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
  projectStatusCode: ProjectStatusCode;
}
export interface IUpdateProjectStatusUpdateReport
  extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
  projectStatusCode?: ProjectStatusCode;
}

export class ProjectStatusUpdateReport extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;
  readonly projectStatusCode: ProjectStatusCode;

  constructor(args: IProjectStatusUpdateReport) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.projectStatusCode = args.projectStatusCode;
  }

  update(
    args: IUpdateProjectStatusUpdateReport,
  ): UpdateProjectStatusUpdateReport {
    return new UpdateProjectStatusUpdateReport({ id: this.id, ...args });
  }
}

export class UpdateProjectStatusUpdateReport extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;
  readonly projectStatusCode?: ProjectStatusCode;

  constructor(
    args: IUpdateProjectStatusUpdateReport & { id: ProjectReportId },
  ) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.projectStatusCode = args.projectStatusCode;
  }
}
